import { translationsImportsMap } from '../services/translationsImportsMap';
import { ErrorHandlerTranslationsOrDefault } from '../services/errorHandlerTranslations';

const resolveMessages = (messages: ErrorHandlerTranslationsOrDefault) =>
  typeof messages.default === 'object' ? messages.default : messages;

export const asyncMessagesLoader = async (locale: string) => {
  const factory =
    translationsImportsMap[locale as keyof typeof translationsImportsMap];

  if (factory == null) {
    throw new Error(
      `@wix/cairo: Failed to find translations for locale '${locale}'`,
    );
  }

  return resolveMessages(await factory());
};
